:root {
  --innerValue: 1496;
  --inner: 1496px;
  --padding-pc: 40px;
  --padding-sp: 35px;
  --base-color: #3b3b3b;
  --white: #ffffff;
  --black: #000000;
  --yellow: #fef30b;
  --yellow-light: #fff982;
  --pink: #efb9b9;
  --green: #0ba500;
  --green-light: #b1f19e;
  --gray: #707070;
  --light: 300;
  --normal: 400;
  --medium: 500;
  --semi-bold: 600;
  --bold: 700;
  --ex-bold: 800;
  --sans-family: "Noto Sans JP", sans-serif;
  --duration: 0.3s ease;
  --header-height-pc: 118px;
  --header-height-sp: 60px;
}

.hoverOpacity {
  transition: opacity var(--duration);
}
@media (any-hover: hover) {
  .hoverOpacity:hover {
    opacity: 0.7;
  }
}

@media screen and (max-width:834px) {
  body:has(#mobile-menu-button[aria-expanded=true]) {
    overflow: hidden;
  }
}

header {
  height: var(--header-height-pc);
}
header * {
  box-sizing: border-box;
}
@media screen and (max-width:834px) {
  header {
    height: var(--header-height-sp);
  }
}
header .header-content {
  height: inherit;
}

.header-left {
  height: inherit;
}
.header-left a {
  display: block;
  height: inherit;
}
.header-left img {
  height: inherit;
}

@media screen and (max-width:834px) {
  .header-right {
    display: flex;
    gap: 10px;
    align-items: center;
  }
}
.header-right .header-shop-menu img {
  width: clamp(150px, calc(86.5280289331px + 16.2748643761vw), 330px);
}
@media screen and (max-width:834px) {
  .header-right .main-navigation {
    display: none;
  }
}
@media screen and (max-width:834px) {
  .header-right .mobile-menu-toggle {
    display: block;
    height: 100%;
  }
}
.header-right .mobile-menu-toggle button {
  display: block;
  padding: 0;
  border: none;
  background: none;
  width: 40px;
  aspect-ratio: 1/1;
  position: relative;
}
.header-right .mobile-menu-toggle button span {
  display: block;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  transform-origin: right center;
  transition: all var(--duration);
}
.header-right .mobile-menu-toggle button span:nth-child(1) {
  top: 20%;
}
.header-right .mobile-menu-toggle button span:nth-child(2) {
  top: 45%;
}
.header-right .mobile-menu-toggle button span:nth-child(3) {
  bottom: 0;
}
.header-right .mobile-menu-toggle button[aria-expanded=true] span:nth-child(1) {
  top: 0%;
  transform: translateX(-60%) rotate(-45deg);
}
.header-right .mobile-menu-toggle button[aria-expanded=true] span:nth-child(2) {
  top: 55%;
  transform: translateX(-60%) rotate(45deg);
}
.header-right .mobile-menu-toggle .menu-toggle-icon {
  display: block;
  width: 80%;
  height: 3px;
}
.header-right .mobile-menu-toggle .menu-toggle-text {
  font-size: 10px;
  margin-left: initial;
  width: 100%;
}

.drawer-menu {
  display: block;
  position: fixed;
  top: var(--header-height-sp);
  bottom: 0;
  right: 0;
  left: 0;
  width: 100%;
  height: calc(100% - var(--header-height-sp));
  overflow-y: scroll;
  z-index: 100;
  background: #efefef;
  pointer-events: none;
  clip-path: inset(0 0 100% 0);
  transition: opacity var(--duration), visibility var(--duration), clip-path var(--duration);
}
@media screen and (min-width:835px) {
  .drawer-menu {
    display: none;
  }
}

.drawer-menu[aria-hidden=false],
.drawer-menu:popover-open {
  pointer-events: auto;
  clip-path: inset(0 0 0 0);
}

.drawer-menu__lists {
  display: block;
  width: 100%;
  margin-inline: auto;
  padding-block: 30px 80px;
  padding-inline: var(--padding-sp);
}

.drawer-menu__list {
  border-bottom: 1px solid var(--base-color);
}
.drawer-menu__list a {
  display: block;
  width: 100%;
  height: 100%;
  padding: 20px 10px;
  text-align: center;
  font-size: 16px;
  position: relative;
  display: flex;
  align-items: center;
  gap: 20px;
  text-align: left;
  color: var(--base-color);
}
.drawer-menu__list a span {
  display: block;
  width: 10px;
  height: 10px;
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translate(0%, -50%) rotate(90deg);
  transition: transform var(--duration);
  color: var(--base-color);
}
.drawer-menu__list a span::before, .drawer-menu__list a span::after {
  content: "";
  display: block;
  width: inherit;
  height: 2px;
  background: currentColor;
  border-radius: 10px;
  position: absolute;
  top: 50%;
  right: 0;
  transform-origin: center right;
}
.drawer-menu__list a span::before {
  transform: translate(0%, -50%) rotate(45deg);
}
.drawer-menu__list a span::after {
  transform: translate(0%, -50%) rotate(-45deg);
}
.drawer-menu__list.is_active a span {
  transform: translate(0%, -50%) rotate(90deg) scale(-1, 1);
}

.footer {
  box-sizing: border-box;
  font-family: var(--sans-family);
  text-align: left;
  padding-block-end: clamp(38px, calc(-3.609403255px + 10.6690777577vw), 156px);
}
@media screen and (max-width:834px) {
  .footer {
    background-color: color-mix(in srgb, var(--gray) 12%, transparent);
  }
}
.footer * {
  box-sizing: border-box;
}

.footer__inner {
  max-width: calc(var(--inner) + var(--padding-pc) * 2);
  margin-inline: auto;
  padding-inline: var(--padding-pc);
}
@media screen and (max-width:834px) {
  .footer__inner {
    padding-inline: var(--padding-sp);
  }
}

.footer__contents {
  border-top: 1px solid var(--gray);
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  padding-block-start: clamp(22px, calc(-12.5569620253px + 8.8607594937vw), 120px);
}
@media screen and (max-width:834px) {
  .footer__contents {
    border-top: none;
    display: block;
  }
}

.footer__main {
  display: flex;
  flex-direction: row-reverse;
  justify-content: space-between;
  column-gap: 20px;
  width: max(600px, 62%);
  padding-inline: min(130px, 6vw) 50px;
}
@media screen and (max-width:834px) {
  .footer__main {
    flex-direction: column;
    width: 100%;
  }
}

@media screen and (max-width:834px) {
  .footer__mainWrap {
    display: contents;
  }
}

@media screen and (max-width:834px) {
  .footer__head {
    order: 1;
    text-align: center;
  }
}

.footer__logo {
  width: clamp(108px, calc(42.4122965642px + 16.8173598553vw), 294px);
}
@media screen and (max-width:834px) {
  .footer__logo {
    display: inline-block;
  }
}
.footer__logo img {
  width: 100%;
  height: auto;
}

.footer__body {
  margin-block-start: 9px;
}
@media screen and (max-width:834px) {
  .footer__body {
    margin-block-start: 30px;
    order: 3;
  }
}

.footer__snsTitle {
  font-size: 20px;
  font-family: var(--sans-family);
  font-weight: var(--ex-bold);
  line-height: 1.45;
  letter-spacing: 0.067em;
}
@media screen and (max-width:834px) {
  .footer__snsTitle {
    padding-block-end: 10px;
    border-bottom: 1px solid var(--gray);
    text-align: center;
  }
}

.footer__snsLists {
  margin-block-start: 14px;
}
@media screen and (max-width:834px) {
  .footer__snsLists {
    margin-block-start: 30px;
  }
}

@media screen and (max-width:834px) {
  .footer__list {
    text-align: center;
  }
}

.footer__list.mt {
  margin-block-start: 50px;
}

.footer__snsList + .footer__snsList {
  margin-block-start: 10px;
}
@media screen and (max-width:834px) {
  .footer__snsList + .footer__snsList {
    margin-block-start: 20px;
  }
}

.footer__snsText {
  font-size: clamp(12px, calc(9.1790235081px + 0.7233273056vw), 20px);
}
@media screen and (max-width:834px) {
  .footer__snsText {
    text-align: center;
  }
}

.footer__snsLink {
  display: block;
  width: 45px;
  margin-block-start: 10px;
  text-align: center;
}
@media screen and (max-width:834px) {
  .footer__snsLink {
    margin-inline: auto;
  }
}

@media screen and (max-width:834px) {
  .footer__bottom {
    order: 2;
  }
}

.footer__adBlock + .footer__adBlock {
  margin-block-start: 15px;
}

.footer__adText {
  font-size: clamp(12px, calc(9.1790235081px + 0.7233273056vw), 20px);
  line-height: 1.45;
}
@media screen and (max-width:834px) {
  .footer__adText {
    text-align: center;
  }
}

.footer__adTel {
  font-size: clamp(20px, calc(14.0054249548px + 1.5370705244vw), 37px);
  line-height: 1.4594594595;
}
@media screen and (max-width:834px) {
  .footer__adTel {
    text-align: center;
  }
}

.footer__sub {
  width: max(400px, 38%);
  padding-inline: 50px;
  border-right: 1px solid var(--gray);
}
@media screen and (max-width:834px) {
  .footer__sub {
    display: none;
  }
}

.footer__listWrap {
  display: flex;
  flex-wrap: wrap;
  gap: 10px 60px;
}

.footer__list {
  margin-block-start: 10px;
}
.footer__list a {
  font-size: 20px;
  line-height: 1.45;
  letter-spacing: 0.067em;
  color: var(--base-color);
  text-decoration: none;
}

.footer__subLists {
  padding-left: 2em;
}

.footer__subList a {
  font-size: 20px;
  line-height: 1.45;
  letter-spacing: 0.067em;
  color: var(--base-color);
  text-decoration: none;
}

.footer__copy {
  font-size: clamp(12px, calc(9.1790235081px + 0.7233273056vw), 20px);
  line-height: 1.45;
  text-align: center;
  margin-block-start: clamp(30px, calc(-54.2766726944px + 21.609403255vw), 269px);
}

.sub {
  color: var(--base-color);
}
.sub .inner {
  max-width: calc(var(--inner) + var(--padding-pc) * 2);
  margin-inline: auto;
  padding-inline: var(--padding-pc);
}
@media screen and (max-width:834px) {
  .sub .inner {
    padding-inline: var(--padding-sp);
  }
}